* {
    box-sizing: border-box;
}
.article {
    margin: 30px;
    padding: 12px;
    background: #bdbdbd;
    border-radius: 5px;
    background-size:cover;
    background-repeat: no-repeat;
}
.image {
    background: transparent;
    /* width: 500px;
    height: 800px; */
    border-radius: 5px;
    padding: 20px;
    color: #bdbdbd;
    font-size: 8rem;
}
.image.right {
    float: right;
    margin-left: 20px;
}
.image.left {
    float: left;
    margin-right: 30px;
}
.content {
    padding: 0 20px;
}
.group:before, .group:after {
    content: "";
    display: table;
}
.group:after {
    clear: both;
}
