  .data-table-row--focusable:focus {
    background: #f7f7f7;
    box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.2);
    outline: none;
  }
  .data-table-row-grid {
    position: relative;
    width: 100%;
    border-top: 1px solid #f7f7f7;
    border-radius: 0;
    height: auto;
    margin: 0;
    padding: 0;
  }
  @media (min-width: 576px) {
    .data-table-row-grid {
      border-left: 1px solid #f7f7f7;
    }
  }
