    .data-table-row-grid.data-table-row--blank-fill:nth-child(1n + 1)
      ~ .data-table-row--blank-fill {
      border-left: 0;
      display: none;
    }
  }
  @media (min-width: 768px) and (max-width: 991.98px) {
    .data-table-row-grid {
      flex: 0 0 50%;
      max-width: 50%;
    }
    .data-table-row-grid:nth-child(odd) {
      border-left: 0;
    }
    .data-table-row-grid.data-table-row--blank-fill:nth-child(odd),
    .data-table-row-grid.data-table-row--blank-fill:nth-child(odd)
      ~ .data-table-row--blank-fill {
      border-left: 0;
      display: none;
    }
  }
  @media (min-width: 992px) and (max-width: 1199.98px) {
    .data-table-row-grid {
      flex: 0 0 33.3333333333%;
      max-width: 33.3333333333%;
    }
    .data-table-row-grid:nth-child(3n + 1) {
      border-left: 0;
    }
    .data-table-row-grid.data-table-row--blank-fill:nth-child(3n + 1),
    .data-table-row-grid.data-table-row--blank-fill:nth-child(3n + 1)
      ~ .data-table-row--blank-fill {
      border-left: 0;
      display: none;
    }
  }
  @media (min-width: 1200px) {
    .data-table-row-grid {
      flex: 0 0 33.3333333333%;
      max-width: 33.3333333333%;
    }
    .data-table-row-grid:nth-child(3n + 1) {
      border-left: 0;
    }
    .data-table-row-grid.data-table-row--blank-fill:nth-child(3n + 1),
    .data-table-row-grid.data-table-row--blank-fill:nth-child(3n + 1)
      ~ .data-table-row--blank-fill {
