  .data-table-row-cards > * {
    margin: 0 auto;
  }
  .data-table-row__tweak-alternate-row-colors {
    margin: 0;
  }
  .data-table-row__tweak-alternate-row-colors:nth-child(odd) {
    background-color: #eee;
