  .data-table {
    color: #2e2e2e;
  }
  .data-table--scrollable {
    overflow: auto;
  }
  .data-table__rows {
    padding: 0;
    margin: 0;
  }
  .data-table__header-container {
    display: flex;
    height: 47px;
    line-height: 47px;
    text-transform: uppercase;
    padding: 0 7px;
  }
  .data-table__header-container--sticky {
    position: relative;
    position: sticky;
    position: -webkit-sticky;
    z-index: 1;
    top: 0;
  }
  .data-table__tweak-no-margin,
  .data-table__tweak-no-margin .data-table__header-container {
    padding: 0;
  }
