    line-height: inherit;
    text-transform: none;
    align-items: center;
  }
  .data-table__tweak-side-borders {
    border-left: 1px solid #d8d8d8;
    border-right: 1px solid #d8d8d8;
  }
  .data-table__tweak-tight-columns .data-table-content,
  .data-table__tweak-tight-columns .data-table-header {
    padding-left: 8px;
    padding-right: 8px;
  }
