    height: 18px;
    margin: 0 4px;
  }
  .data-table-grid {
    display: flex;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
  }
  .data-table-grid__rows {
    display: flex;
    flex-wrap: wrap;
    padding: 0;
    width: 100%;
  }
  .data-table-cards {
    display: flex;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
  }
  .data-table-cards__rows {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin: 0 15px;
  }
  
  .data-table-header--default {
    white-space: nowrap;
    display: flex;
    font-size: 12px;
    font-weight: 700;
  }
  .data-table-header__sort-icon {
    margin: 8px;
    display: inline-block;
    vertical-align: middle;
    font-size: 18px;
    width: 12px;
  }
  .data-table-header__sortable {
    cursor: pointer;
  }
  .data-table-content {
    padding: 0 15px;
    text-overflow: ellipsis;
    justify-content: flex-start;
    align-items: center;
    display: flex;
    min-width: 0;
    flex-wrap: wrap;
    flex-flow: row;
  }
  .data-table-content__end {
    justify-content: flex-end;
    padding-right: 0;
  }
  .data-table-content__no-margin:first-child {
    padding-left: 0;
  }
  .data-table-content__no-margin:last-child {
    padding-right: 0;
  }
  .data-table-content-cards {
    border-radius: 6px;
    border: 1px solid #efefef;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
    height: auto;
    padding: 0;
    flex: 1 1;
    align-items: flex-start;
  }
  .data-table-content-grid {
    padding: 0;
  }
  
